#home {
  background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.6)),
    url(C:\Users\gokul\Desktop\R\my-app\src\images\bg.jpg);
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  min-height: calc(100vh);
  padding-top: 120px;
  flex-direction: column;
}
#home h1 {
  font-size: 10vh;
  color: white;
  letter-spacing: 3px;
  text-align: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  text-shadow: 0px 1px 0px #afaaaa, 0px 2px 0px #afaaaa, 0px 3px 0px #afaaaa,
    0px 4px 0px #afaaaa, 0px 5px 0px #afaaaa, 0px 6px 0px #afaaaa,
    0px 7px 0px #afaaaa;
}
#home p {
  font-size: 5vh;
  color: white;
  text-align: center;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.brand {
  color: #29f700;
}

#home .input-group {
  width: 50%;
  height: 45px;
}
#home input {
  border-radius: 50px;
}
.signin {
  margin-left: 2% !important;
  border-radius: 10px !important;
  background-color: #29f700;
  color: black;
}